import * as React from 'react';
import { IndentProperties, unitsConverter, IndUtil } from '@ali/4ever-utils';
import { InputNumber, Select } from '@ali/we-design';
import logger from '@ali/4ever-logger';
import { ConfigItem } from '../styled';
import {
  ParagraphSetCompProps,
  commonInputNumberProps,
  inputNumberStyle,
  commonSelectStyle,
  UnitSelect,
  formatNumber10,
  checkInputValue,
  InputNumberChangeType,
  parseInputNumberPara,
} from './common';
import { Controller } from '@ali/4ever-cangjie';

/**
 * 右侧面板允许的范围：
 * 比 tab 等交互更大
 * 比word/wps 允许的范围更小，溢出不可见没有意义？
 * 70：A3 横向 窄页边距 时的宽度
 */
const MAX_CHARS = 70;
const MAX = unitsConverter.toPT(`${IndUtil.charsToPx(MAX_CHARS * 100)}px`); // pt
type IND_TYPE = 'left' | 'right' | 'special';

export default function IndSet(props: ParagraphSetCompProps) {
  const controller = Controller.useController();
  const { locale } = props;
  const {
    paragraph: pLocale = {},
    indent: iLocale = {},
  } = locale;
  const unitsLocale = [{
    key: 'chars', value: pLocale.charsUnit,
  }, {
    key: 'pt', value: pLocale.ptUnit,
  }];
  const {
    left,
    leftChars,
    right,
    rightChars,
    firstLine,
    firstLineChars,
    hanging,
    hangingChars,
  } = controller.query('getCommonInd') as IndentProperties;
  let specialType = 'none';

  const units: Record<IND_TYPE, string> = {
    left: 'chars',
    right: 'chars',
    special: 'chars',
  };
  const values: Record<IND_TYPE, number | undefined> = {
    left: undefined,
    right: undefined,
    special: undefined,
  };

  if (hanging || hangingChars) {
    // hanging > firstLine
    specialType = 'hanging';
    units.special = (hangingChars || !hanging) ? 'chars' : 'pt';
    values.special = hangingChars ? hangingChars / 100 : unitsConverter.toPT(`${hanging}px`);
  } else if (firstLine || firstLineChars) {
    specialType = 'firstLine';
    units.special = (firstLineChars || !firstLine) ? 'chars' : 'pt';
    values.special = firstLineChars ? firstLineChars / 100 : unitsConverter.toPT(`${firstLine}px`);
  } else {
    units.special = 'chars';
  }

  units.left = (leftChars || !left) ? 'chars' : 'pt';
  if (leftChars) {
    values.left = leftChars / 100;
  } else if (left !== undefined) {
    values.left = unitsConverter.toPT(`${left}px`);
  }
  units.right = (rightChars || !right) ? 'chars' : 'pt';
  if (rightChars) {
    values.right = rightChars / 100;
  } else if (right !== undefined) {
    values.right = unitsConverter.toPT(`${right}px`);
  }

  const createIndChangeHandler = (type: string) => {
    return (arg: InputNumberChangeType) => {
      let val = parseInputNumberPara(arg);
      if (!checkInputValue(val)) return;
      // 过滤「相同值」or「特殊缩进值为 0 且之前为 undefined」时的情况，提前 return
      if (val === values[type] || (type === 'special' && val === 0 && values[type] === undefined)) return;
      const nextInd: IndentProperties = {};
      let key = type;
      if (type === 'special') {
        key = specialType === 'none' ? 'firstLine' : specialType;
      }

      const isCharsUnit = units[type] === 'chars';
      val = Math.min(val, isCharsUnit ? MAX_CHARS : MAX);

      if (isCharsUnit) {
        // 换算为与导入一致的单位
        nextInd[`${key}Chars`] = val * 100;
        // 换算兜底
        nextInd[`${key}`] = IndUtil.charsToPx(nextInd[`${key}Chars`]);
      } else {
        nextInd[`${key}`] = unitsConverter.toPX(`${val}pt`);
      }
      controller.dispatch('setParagraphInd', nextInd);
      logger.sum(`paragraph_sidebar_ind_${type}`);
    };
  };

  const handleLeftChange = createIndChangeHandler('left');
  const handleRightChange = createIndChangeHandler('right');
  const handleIndChange = createIndChangeHandler('special');

  const createIndUnitChangeHandler = (type: string) => {
    return (unit: string) => {
      // 不允许在未设置首行或缩进的时候改单位
      if (type === 'special' && specialType === 'none') return;

      const isCharsUnit = units[type] === 'chars';
      const max = isCharsUnit ? MAX : MAX_CHARS;
      const nextInd: IndentProperties = {};
      // 默认 2 字符 24 pt
      let nextValue = isCharsUnit ? 24 : 2;
      if (values[type]) {
        nextValue = Math.min(values[type], max);
      }

      const key = type !== 'special' ? type : specialType;

      if (unit === 'pt') {
        nextInd[`${key}`] = unitsConverter.toPX(`${nextValue}pt`);
      } else if (unit === 'chars') {
        nextInd[`${key}Chars`] = nextValue * 100;
        // 兜底
        nextInd[`${key}`] = IndUtil.charsToPx(nextInd[`${key}Chars`]);
      }
      controller.dispatch('setParagraphInd', nextInd);
      logger.sum(`paragraph_sidebar_ind_${type}_unit_${unit}`);
    };
  };

  const handleLeftUnitChange = createIndUnitChangeHandler('left');
  const handleRightUnitChange = createIndUnitChangeHandler('right');
  const handleIndUnitChange = createIndUnitChangeHandler('special');

  const handleIndTypeChange = (type: string) => {
    if (type === 'none') {
      controller.dispatch('setParagraphInd', {
        firstLine: 0,
        firstLineChars: 0,
        hanging: 0,
        hangingChars: 0,
      });
    } else {
      const nextInd: IndentProperties = {};
      // 默认两字符
      nextInd[`${type}Chars`] = 200;
      nextInd[`${type}`] = IndUtil.charsToPx(nextInd[`${type}Chars`]); // 兜底
      controller.dispatch('setParagraphInd', nextInd);
    }
    logger.sum(`paragraph_sidebar_ind_special_${type}`);
  };

  return (
    <>
      <ConfigItem>
        <span className="label">{iLocale.leftIndent}</span>
        <InputNumber
          value={values.left}
          formatter={formatNumber10}
          {...commonInputNumberProps}
          style={inputNumberStyle}
          onStep={handleLeftChange}
          onPressEnter={handleLeftChange}
          onBlur={handleLeftChange}
          key={`left_${values.left}`}
        />
        <UnitSelect value={units.left} units={unitsLocale} onChange={handleLeftUnitChange} />
      </ConfigItem>
      <ConfigItem>
        <span className="label">{iLocale.rightIndent}</span>
        <InputNumber
          value={values.right}
          formatter={formatNumber10}
          {...commonInputNumberProps}
          style={inputNumberStyle}
          onStep={handleRightChange}
          onPressEnter={handleRightChange}
          onBlur={handleRightChange}
          key={`right_${values.right}`}
        />
        <UnitSelect value={units.right} units={unitsLocale} onChange={handleRightUnitChange} />
      </ConfigItem>
      <ConfigItem>
        <Select
          defaultValue="none"
          value={specialType}
          style={commonSelectStyle}
          onChange={handleIndTypeChange}
        >
          <Select.Option value="none">{iLocale.noneIndent}</Select.Option>
          <Select.Option value="firstLine">{iLocale.firstLineIndent}</Select.Option>
          <Select.Option value="hanging">{iLocale.hangIndent}</Select.Option>
        </Select>
        <InputNumber
          value={values.special}
          {...commonInputNumberProps}
          formatter={formatNumber10}
          style={inputNumberStyle}
          onPressEnter={handleIndChange}
          onBlur={handleIndChange}
          onStep={handleIndChange}
          key={`special_${values.special}`}
        />
        <UnitSelect value={units.special} units={unitsLocale} onChange={handleIndUnitChange} />
      </ConfigItem>
    </>
  );
}
